/* ----------------------------------------------------------------------------
 * Structure
 * ------------------------------------------------------------------------- */
main {
  position: relative;
  height: 100%; }
  main .wrapper {
    height: 50vh;
    top: 25vh;
    width: 100%;
    position: absolute; }

header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  display: none; }
  header.active-header {
    display: block; }
  header #pagination {
    width: 100%;
    text-align: center;
    margin-top: 3vh; }
  header nav {
    width: 100%;
    display: block;
    position: relative; }
    header nav:after {
      content: '';
      position: absolute;
      top: calc(50% - 2px);
      width: 100%;
      height: 4px;
      background-color: #ee906e;
      z-index: 1; }
    header nav ul {
      width: 100%;
      padding: 10px 40px;
      display: flex;
      align-items: center;
      flex-flow: row wrap;
      justify-content: space-between; }
      header nav ul li {
        width: 30px;
        height: 30px;
        background-color: #ee906e;
        border-radius: 15px;
        z-index: 2;
        cursor: default; }
        header nav ul li.active-nav {
          background-color: #ffffff;
          cursor: pointer; }

section {
  display: none;
  width: 100%;
  min-height: 100%;
  position: absolute;
  background-color: rgba(201, 62, 11, 0.8); }
  section.active-section {
    display: block; }
  section.focused .section-input {
    font-size: 50px;
    border: 1px solid #ee906e;
    background-color: rgba(238, 144, 110, 0.4); }
  section.focused .input-description {
    font-size: 18px;
    margin-top: 0; }
  section.focused .measurements {
    height: 8vh; }
  section h1 {
    font-size: 24px;
    width: 100%;
    padding: 0 40px;
    text-align: center;
    line-height: 1.3;
    display: block;
    margin-bottom: 4vh; }
  section .measurements {
    height: 5vh;
    width: 100%;
    text-align: center; }
    section .measurements span {
      color: #ee906e;
      text-transform: uppercase;
      padding: 10px;
      transition: all 0.3s linear; }
  section .section-input {
    width: calc(100% - 80px);
    text-align: center;
    font-family: "Montserrat", Helvetica, Arial, sans-serif;
    font-size: 130px;
    padding: 10px;
    border: 1px solid #ee906e;
    margin: 0 0 0 40px;
    font-weight: 700;
    text-align: center;
    transition: all 0.3s linear; }
  section .input-imperial {
    display: none;
    width: calc(100% + 40px); }
    section .input-imperial .section-input[name='height-ft'] {
      width: calc(40% - 62px); }
    section .input-imperial .section-input[name='height-inch'] {
      width: calc(60% - 82px);
      margin-left: 20px; }
  section .input-description {
    width: 100%;
    text-align: center;
    font-size: 24px;
    margin-top: -40px;
    text-transform: uppercase; }
    section .input-description > span,
    section .input-description > div {
      display: none; }

h1 {
  font-weight: 700;
  margin: 0;
  line-height: 0.9; }

#home {
  background-color: transparent; }
  #home .wrapper {
    width: auto;
    height: auto;
    left: 50%;
    transform: translateX(-50%);
    top: auto;
    bottom: 25vh; }
  #home h1 {
    font-size: 40px;
    text-transform: uppercase;
    position: relative;
    padding: 0;
    text-align: left; }
  #home h2 {
    font-weight: 300;
    color: #c93e0b;
    position: relative;
    padding-left: 2px; }

.active-metric span.metric,
.active-imperial span.imperial {
  color: #ffffff;
  text-decoration: underline; }

.active-metric .input-metric,
.active-imperial .input-imperial,
.active-metric .description-metric,
.active-imperial .description-imperial {
  display: inline-block; }

.active-metric .input-imperial,
.active-imperial .input-metric {
  display: none; }

.description-imperial.i-height {
  width: 100%; }
  .description-imperial.i-height span:nth-child(1) {
    width: calc(40% - 46px);
    margin-left: 40px;
    float: left; }
  .description-imperial.i-height span:nth-child(2) {
    width: calc(60% - 58px);
    margin-left: 24px;
    float: left; }

